etween: 20,\r\n          slidesPerView: 3,\r\n        },\r\n        1200:{\r\n          spaceBetween: 50,\r\n          slidesPerView: 4,\r\n        }\r\n      }\r\n    });\r\n  }\r\n}","export function dropDownLestTel(){\r\n  const linkDropDown = document.querySelector('.js-header-tel');\r\n  const dropdownListTel = document.querySelector('.js-dropdown-list-tel')\r\n  if(linkDropDown){\r\n    linkDropDown.addEventListener('click',function(e){\r\n      dropdownListTel.classList.toggle('active')\r\n    });\r\n  }\r\n};","export function joicesCustome(){\r\n  const elements = document.querySelectorAll('.js-choice');\r\n  elements.forEach(function(el){\r\n    const choices = new Choices(el,{\r\n      searchEnabled: false,\r\n      classNames:{\r\n        containerOuter:'choices search-form__select'\r\n      }\r\n    });\r\n  });\r\n}","export function modals() {\r\n  function bindModal(triggerSelector, modalSelector, closeSelector) {\r\n    const triggerModalCallback = document.querySelectorAll(triggerSelector);\r\n    const modalCallback = document.querySelector(modalSelector);\r\n    const buttonClose = document.querySelector(closeSelector);\r\n    //.js-modal-callback-trigger\r\n    //.js-modal-callback\r\n    //.js-button-close\r\n    triggerModalCallback.forEach(function (el) {\r\n      el.addEventListener('click', function (e) {\r\n        if (e.target) {\r\n          e.preventDefault();\r\n          modalCallback.style.display = 'flex';\r\n          document.body.classList.add('js-modal-open')\r\n        }\r\n      });\r\n    });\r\n    buttonClose.addEventListener('click', function () {\r\n      modalCallback.style.display = 'none';\r\n      document.body.classList.remove('js-modal-open')\r\n    });\r\n    modalCallback.addEventListener('click', function (e) {\r\n      if (e.target === modalCallback) {\r\n        modalSelector.style.display = 'none';\r\n        document.body.classList.remove('js-modal-open')\r\n      }\r\n    });\r\n  }\r\n  bindModal('.js-modal-callback-trigger', '.js-modal-callback', '.js-button-close')\r\n}","export function playVideo(){\r\n  const videoBoxes = document.querySelectorAll('.js-video');\r\n  const videoPlayButtons = document.querySelectorAll('.js-video-play')\r\n  videoBoxes.forEach(function(el){\r\n    el.addEventListener('click',function(e){\r\n      if(e.target.classList.contains('js-video-play'))\r\n      el.insertAdjacentHTML(\"beforeend\",'<iframe class=\"videos\" width=\"100%\" height=\"100%\" src=\"https://www.youtube.com/embed/1GVGWJqAWlQ?autoplay=1\" title=\"YouTube video player\" frameborder=\"0\" allow=\"autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen></iframe>')\r\n      el.querySelector('.js-video-play').remove()\r\n    });\r\n  });\r\n}","export function tabOffes(){\r\n  const offerButtons  = document.querySelectorAll('[data-tab]');\r\n  const tabContents = document.querySelectorAll(`[data-content]`);\r\n  if(offerButtons.length){\r\n    offerButtons.forEach((el,index)=>{\r\n      if(index === 0){\r\n        el.classList.add('active')\r\n      }\r\n      el.addEventListener('click',function(){\r\n        const currentButton = this.dataset.tab;\r\n        const tabContent = document.querySelector(`[data-content=\"${currentButton}\"]`)\r\n        offerButtons.forEach(function(btn){\r\n          btn.classList.remove('active')\r\n        });\r\n        this.classList.add('active');\r\n  \r\n        tabContents.forEach((elem)=>{\r\n          elem.classList.add('js-offers-tab-hidden')\r\n        });\r\n        tabContent.classList.remove('js-offers-tab-hidden');       \r\n      });\r\n    });\r\n  }\r\n}","// The module cache\nvar __webpack_module_cache__ = {};\n\n// The require function\nfunction __webpack_require__(moduleId) {\n\t// Check if module is in cache\n\tvar cachedModule = __webpack_module_cache__[moduleId];\n\tif (cachedModule !== undefined) {\n\t\treturn cachedModule.exports;\n\t}\n\t// Create a new module (and put it into t